    font-size: .8em;
    padding: 1em; }
    header nav a:hover {
      color: darkgray; }

footer {
  text-align: center;
  font-size: .6em;
  position: fixed;
  bottom: 1em;
  width: 100%; }
  footer a {
    text-decoration: none;
    color: black; }
    footer a:hover {
